    }

    .blog-details-inner .posted-by .pb-pic {
        float: none;
        margin-bottom: 30px;
    }

    .blog-details-inner .posted-by .pb-text {
        padding-left: 0;
    }

    .blog-sidebar {
        padding-top: 50px;
    }
}

/* Small Device: 320px */

@media only screen and (max-width: 479px) {


    .blog-details-inner .blog-post .next-blog .nb-pic i {
